/*eslint-disable*/
import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// react component used to create nice image meadia player
// import ImageGallery from "react-image-gallery";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// import FormControl from "@material-ui/core/FormControl";
// import Select from "@material-ui/core/Select";
// import MenuItem from "@material-ui/core/MenuItem";
// import List from "@material-ui/core/List";
// import ListItem from "@material-ui/core/ListItem";
// @material-ui/icons
import ShoppingCart from "@material-ui/icons/ShoppingCart";
// import LocalShipping from "@material-ui/icons/LocalShipping";
// import VerifiedUser from "@material-ui/icons/VerifiedUser";
// import Favorite from "@material-ui/icons/Favorite";
// core components
import Header from "components/Header/Header.js";
import HeaderLinks from "components/Header/HeaderLinks.js";
import Parallax from "components/Parallax/Parallax.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
// import Footer from "components/Footer/Footer.js";
import Button from "components/CustomButtons/Button.js";
// import Accordion from "components/Accordion/Accordion.js";
// import InfoArea from "components/InfoArea/InfoArea.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import Tooltip from "@material-ui/core/Tooltip";
import FloatyAssistant from "components/Assistant/floaty";

import productStyle from "assets/jss/material-kit-pro-react/views/productStyle.js";

// images
import cardProduct1 from "assets/img/coffee1.jpg";



const useStyles = makeStyles(productStyle);

const DoormanProductPage = () => {
  // const [colorSelect, setColorSelect] = React.useState("0");
  // const [sizeSelect, setSizeSelect] = React.useState("0");
  const classes = useStyles();

  return (
    <div className={classes.productPage}>
      <Header
        brand="KLK COFFEE"
        links={<HeaderLinks dropdownHoverColor="dark" />}
        fixed
        color="transparent"
        changeColorOnScroll={{
          height: 100,
          color: "dark"
        }}
      />
      <Parallax
        image={require("assets/img/bg50.jpg")}
        filter="dark"
        className={classes.pageHeader}
        // style={{ minHeight: '90vh', maxHeight: '300px' }}
      >
      </Parallax>
      <div className={classNames(classes.section, classes.sectionGray)}>
        <div className={classes.container}>
          <div className={classes.relatedProducts}>
            <GridContainer>
              <GridItem sm={6} md={3}>
                <Card product>
                  <CardHeader image>
                    <a href="#pablo">
                      <img src={cardProduct1} alt="cardProduct" />
                    </a>
                  </CardHeader>
                  <CardBody>
                    <h4 className={classes.cardTitle}>衣索比亞 瑰夏村</h4>
                    <div className={classes.cardDescription}>
                      <p>Bench Maji,Ethiopia</p>
                      <p>Gesha</p>
                      <p>Process - Washed</p>
                      <p>花香、杏桃、蜂蜜、水果</p>
                    </div>
                  </CardBody>
                  <CardFooter className={classes.justifyContentBetween}>
                    <div className={classes.price}>
                      <h4>$580</h4>
                    </div>
                    <div className={classes.stats}>
                      <Tooltip
                        id="tooltip-top"
                        title="Save to Wishlist"
                        placement="top"
                        classes={{ tooltip: classes.tooltip }}
                      >
                        <Button color="info" className={classes.floatRight}>
                          <ShoppingCart /> 0 items
                        </Button>
                      </Tooltip>
                    </div>
                  </CardFooter>
                </Card>
              </GridItem>
            </GridContainer>
          </div>
        </div>
      </div>
      <FloatyAssistant></FloatyAssistant>
    </div>
  );
}

export default DoormanProductPage;